import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import Swal from 'sweetalert2';
import {Constants} from '../../../app.constants';
import {MinistryManagementService} from '../../../services/ministry-management.service';
import {Router} from '@angular/router';
import {IMinistry} from '../../../interfaces/interfaces';
import {AuthService} from '../../../services/auth.service';
import {AppData} from '../../../data';

@Component({
    selector: 'app-ministry-list',
    templateUrl: './ministry-list.component.html',
    styleUrls: ['./ministry-list.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class MinistryListComponent implements OnInit {

    public noMinistry;
    ministries: IMinistry[];
    errorMessage = '';
    loading: boolean;
    queryString: '';
    searchableList = ['name', 'code'];


    constructor(private ministryManagementService: MinistryManagementService,
                private router: Router) {
    }

    ngOnInit() {
        this.getMinistries()
    }


    getMinistries() {
        this.loading = true;
        this.errorMessage = '';
        this.ministries = [];
        this.ministryManagementService.getMinistries(this.queryString).subscribe((ministries: any) => {
            this.ministries = ministries;
            this.loading = false;
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
            this.loading = false;
        })
    }

    deleteMinistry(id, index) {
        Swal.fire({
            title: 'Are you sure?',
            text: 'Do you want to delete this record?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.ministryManagementService.deleteMinistry(id).subscribe(deleted => {
                        Swal.fire('Deleted!', 'Institution has been deleted!', 'success');
                        this.ministries.splice(index, 1);
                    }, (err) => {
                        Constants.handleError(err, this.router, true);
                    }
                )
            }
        });
    }

    can(permission) {
        return AuthService.can(permission);
    }

    get permissions() {
        return AppData.permissions;
    }

}
