import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MinistryManagementService} from '../../../services/ministry-management.service';
import {Router} from '@angular/router';
import {ICity, IMinistry, IState} from '../../../interfaces/interfaces';
import Swal from 'sweetalert2';
import {Constants} from '../../../app.constants';

@Component({
    selector: 'app-create-ministry',
    templateUrl: './create-ministry.component.html',
    styleUrls: ['./create-ministry.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class CreateMinistryComponent implements OnInit {

    public NewMinistryForm: FormGroup;
    errorMessage: any[];
    loading: boolean;
    states: IState[];
    cities: ICity[] = [];

    constructor(private formBuilder: FormBuilder,
                private ministryManagementService: MinistryManagementService,
                private router: Router) {
    }


    ngOnInit() {
        this.buildForm();
        this.getStates();
    }

    buildForm() {
        this.NewMinistryForm = this.formBuilder.group({
            name: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            email: ['', Validators.compose([Validators.required, Validators.email])],
            phone: ['', Validators.compose([Validators.required, Validators.minLength(8), Validators.maxLength(20)])],
            state_id: ['', Validators.required],
            city_id: ['', Validators.compose([Validators.required])],
            address_line_1: ['', Validators.required],
            address_line_2: [''],
            pobox_number: [''],
        })
    }

    public getStates(): void {
        this.ministryManagementService.getStates().subscribe((states: IState[]) => {
            this.states = states;
        });
    }

    public onSubmit(value: IMinistry): void {
        this.loading = true;
        this.ministryManagementService.addMinistry(value).subscribe((ministry: IMinistry) => {
            if (ministry) {
                Swal.fire('Created!', 'You have just added a new parastatal!', 'success');
                this.router.navigate(['/app/ministries'])
            } else {
                this.loading = false;
            }
        }, (err) => {
            Constants.handleError(err, this.router, true);
            this.loading = false;
        })
    }

    changeCities(value) {
        this.cities = [];
        if (value) {
            for (let x = 0; x < this.states.length; x++) {
                if (this.states[x].id.toString() === value.toString()) {
                    this.cities = this.states[x].cities;
                    break;
                }
            }
        }
    }
}
