import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {MinistryListComponent} from './ministry-list/ministry-list.component';
import {CreateMinistryComponent} from './create-ministry/create-ministry.component';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {RouterModule, Routes} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {EditMinistryComponent} from './edit-ministry/edit-ministry.component';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';
import {LoaderModule} from '../../shared/loader/loader.module';

export const routes: Routes = [
    {path: 'create', component: CreateMinistryComponent},
    {path: 'edit/:id', component: EditMinistryComponent},
    {path: '', redirectTo: 'list', pathMatch: 'full'},
    {path: 'list', component: MinistryListComponent},
    {path: 'parastatals',
        loadChildren: () => import('app/pages/institution/parastatal/parastatal.module').then(m => m.ParastatalModule),
        data: {breadcrumb: 'Institution/Parastatal'}
    }
];

@NgModule({
    declarations: [MinistryListComponent, CreateMinistryComponent, EditMinistryComponent],
    imports: [
        CommonModule,
        PerfectScrollbarModule,
        RouterModule.forChild(routes),
        FormsModule,
        ReactiveFormsModule,
        CustomErrorModule,
        LoaderModule,
    ]
})
export class InstitutionModule {
}
