import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';
import {ICity, IMinistry, IState} from '../../../interfaces/interfaces';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MinistryManagementService} from '../../../services/ministry-management.service';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
    selector: 'app-edit-ministry',
    templateUrl: './edit-ministry.component.html',
    styleUrls: ['./edit-ministry.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class EditMinistryComponent implements OnInit {

    errorMessage: any[];
    loading: boolean;
    pageLoading: boolean;
    states: IState[] = [];
    cities: ICity[] = [];
    public UpdateMinistryForm: FormGroup;
    ministry: IMinistry = {
        name: '',
        code: '',
        email: '',
        phone: '',
        address: {
            address_line_1: '',
            address_line_2: '',
            state_id: 0,
            city_id: 0,
            state: '',
            city: '',
            pobox_number: ''
        }
    }

    constructor(private formBuilder: FormBuilder,
                private route: ActivatedRoute,
                private router: Router,
                private ministryManagementService: MinistryManagementService) {
    }

    ngOnInit() {
        const id = this.route.snapshot.params['id'];
        this.getMinistryById(id);
        this.getStates();
        this.buildForm();
    }

    buildForm() {
        this.UpdateMinistryForm = this.formBuilder.group({
            name: [this.ministry.name, Validators.compose([Validators.required, Validators.maxLength(255)])],
            code: [this.ministry.code, Validators.compose([Validators.required, Validators.maxLength(255)])],
            email: [this.ministry.email, Validators.compose([Validators.required, Validators.email])],
            phone: [this.ministry.phone, Validators.compose([Validators.required])],
            state_id: [this.ministry.address ? this.ministry.address.state_id : '', Validators.required],
            city_id: [this.ministry.address ? this.ministry.address.city_id : '', Validators.compose([Validators.required])],
            address_line_1: [this.ministry.address ? this.ministry.address.address_line_1 : '', Validators.required],
            address_line_2: [this.ministry.address ? this.ministry.address.address_line_2 : ''],
            pobox_number: [this.ministry.address ? this.ministry.address.pobox_number : ''],
        })

    }

    getStates() {
        this.ministryManagementService.getStates()
            .subscribe((states: IState[]) => {
                    this.states = states
                },
                (err) => {
                    Constants.handleError(err, this.router, true);
                },
            );
    }

    getMinistryById(id: number) {
        this.pageLoading = true;
        this.ministryManagementService.getMinistryByID(id, 'states').subscribe((data: any) => {
            this.ministry = data.data;
            this.states = data.states;

            this.pageLoading = false;
            if (this.ministry.address) {
                this.changeCities(this.ministry.address.state_id);
            }
            this.UpdateMinistryForm.patchValue({
                name: this.ministry.name,
                code: this.ministry.code,
                email: this.ministry.email,
                phone: this.ministry.phone,
                state_id: this.ministry.address ? this.ministry.address.state_id : '',
                city_id: this.ministry.address ? this.ministry.address.city_id : '',
                state: this.ministry.address ? this.ministry.address.state : '',
                city: this.ministry.address ? this.ministry.address.city : '',
                address_line_1: this.ministry.address ? this.ministry.address.address_line_1 : '',
                address_line_2: this.ministry.address ? this.ministry.address.address_line_2 : '',
                pobox_number: this.ministry.address ? this.ministry.address.pobox_number : ''
            });
        }, (err) => {
            Constants.handleError(err, this.router, true);
            this.pageLoading = false;
        });
    }

    changeCities(value) {
        this.cities = [];
        if (value) {
            for (let x = 0; x < this.states.length; x++) {
                if (this.states[x].id.toString() === value.toString()) {
                    this.cities = this.states[x].cities;
                    break;
                }
            }
        }
    }

    public onSubmit(value: IMinistry): void {
        value.id = this.ministry.id;
        const id = this.route.snapshot.params['id'];
        this.loading = true;
        if (value.id) {
            this.ministryManagementService.updateMinistry(value).subscribe((ministry: any) => {
                if (ministry) {
                    Swal.fire('Update!', 'Institution Information Updated', 'success');
                }

                this.loading = false;
            }, (err) => {
                Constants.handleError(err, this.router, true);
                this.loading = false;
            })
        }
    }

}
